  background: #0056b3;
  color: white;
  padding: 10px 15px;
  z-index: 1001;
  transition: top 0.3s ease-in-out;
  border-bottom-right-radius: 5px;
}

.skip-link:focus {
  top: 0;
}

.loading-indicator {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 2rem;
  font-size: 1.2em;
  color: #555;
}
.loading-indicator.global-loading {
  min-height: 200px;
}
.loading-indicator .spinner {
  border: 4px solid rgba(0, 0, 0, 0.1);
  width: 36px;
  height: 36px;
